import { createWebHistory, createRouter } from "vue-router";
import Player from './components/PlayerWrap.vue'
import Team from './components/TeamWrap.vue'
import Schedule from './components/ScheduleWrap.vue'

const routes = [
    {
        path: '/',
        component: Player
    },
    {
        path:'/team',
        component: Team
    },
    {
        path:'/schedule',
        component: Schedule
    },
    
]
const router = createRouter({
    history: createWebHistory(), routes,
   });
   export default router; 