<template lang="">
  <div id="container">
    <h2>player</h2>
    <div class="in_wrap">
      <div class="content" v-for="(p,i) in player" :key="i">
          <h3 @click="modalOpen(i)">{{p.title}}</h3>
          <img @click="modalOpen(i)" :src="p.image" :alt="p.id">
          <Modal v-if="modal" :modal="modal" :player="player"
              :crt="crt" @closeModal="modalClose">
          </Modal>
      </div>
    </div>
    <div v-if="show">
    <button class="more" @click="more">더보기</button>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import player from '../assets/player.js'
import Modal from './PlayerModalWrap.vue'
export default {
  data() {
    return {
      player: player,
      modal:false,
      crt:0,
      moreList:0,
      show: true
    }
  },
  components:{
        Modal
    },
    methods: {
      modalOpen(i) {
        this.crt=i
        this.modal = true
      },
      modalClose(){
        this.modal = false
      },
      more(){
        axios.get(`https://homerking-092.github.io/data/soccer${this.moreList}.json`)
        .then( result => {
          console.log(result.data);
          this.player.push(result.data);
          this.show = !this.show;
          // if (this.moreList == 1) {
          //   this.moreList = 0
          // }
      })
      }
    },
  
}
</script>
<style>
  #container {
 margin-bottom: 10rem
 }
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: 200px;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
 .more {
    border: none;
    color: rgb(77, 77, 77);
    border-radius: 2rem;
    padding: 0 1rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 3rem;
    cursor: pointer;
  }
</style>