<template>
    <div id="container">
        <h2>
            99DAN
        </h2>
            <hr>
            <hr>
            <hr>
            <div>
                <h3>{{first}} * {{second}} = ?</h3>
                <input type="number" v-model="value" placeholder="숫자입력">
                <button class="input_btn" type="submit" @click.prevent="onSubmit" @keyup.enter="onSubmit">입력</button>
            <div id="result">
                <h3>입력값: {{value}}</h3>
                <h3>결과: {{result}}</h3>
            </div>
            <hr>
            <hr>
            <hr>
        </div>
    </div>
    
</template>
<script>
  export default {
     data(){
        return{
        first : 1 + (Math.ceil(Math.random() * 8)),
        second : Math.ceil(Math.random() * 8),
        result : '',
        value : ''
        }
    },
    methods: {
        onSubmit(){
            if(this.first * this.second == parseInt(this.value)){
                this.result = '정답'
                this.value = ''
                this.first = 1 + (Math.ceil(Math.random() * 8))
                this.second = Math.ceil(Math.random() * 8)
            }else{
                this.result = '오답'
                this.value = ''
            }
            
        }
    },
  }
</script>
<style>
body {
  padding: 1rem;
}
  #container {
 margin-bottom: 10rem
 }
 
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: auto;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
 .input_btn{
     border: none;
    color: rgb(77, 77, 77);
    border-radius: 2rem;
    padding: 0 1rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 3rem;
    cursor: pointer;
 }
</style>